import React, { useCallback, useRef } from "react";
import styled from "styled-components";
import {
  Heading,
  CardBody,
  Flex,
  ArrowForwardIcon,
  Skeleton,
} from "@pancakeswap-libs/uikit";
import { NavLink } from "react-router-dom";
import useI18n from "hooks/useI18n";
import BigNumber from "bignumber.js";
import { QuoteToken } from "config/constants/types";
import {
  useFarms,
  usePriceBnbBusd,
  useFarmCakePerBlock,
  usePriceCakeBusd,
  usePriceX2PBusd,
} from "state/hooks";
import { BLOCKS_PER_YEAR, CAKE_POOL_PID } from "config";
import Card from "components/custom/Card";

const StyledFarmStakingCard = styled(Card)`
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  background-image: url(/images/infographic.png);
  background-size: 30%;
  background-position: 75% 50%;
  background-repeat: no-repeat;

  @media only screen and (min-width: 792px) {
    background-size: 70%;
    background-position: 50% 80%;
    background-repeat: no-repeat;
  }

  ${({ theme }) => theme.mediaQueries.lg} {
    margin: 0;
    max-width: none;
  }
`;
const CardMidContent = styled(Heading).attrs({ size: "xl" })`
  line-height: 44px;
`;
const EarnAPYCard = () => {
  const TranslateString = useI18n();
  const farmsLP = useFarms();
  const bnbPrice = usePriceBnbBusd();
  const rewardPerBlock = useFarmCakePerBlock();
  const cakePrice = usePriceCakeBusd();
  const x2pPrice = usePriceX2PBusd();

  const maxAPY = useRef(Number.MIN_VALUE);

  const getHighestAPY = () => {
    calculateAPY(farmsLP);
    return (maxAPY.current * 100).toLocaleString("en-US").slice(0, -1);
  };

  const calculateAPY = useCallback(
    (farmsToDisplay) => {
      const cakePriceVsBNB = new BigNumber(
        farmsLP.find((farm) => farm.pid === CAKE_POOL_PID)?.tokenPriceVsQuote ||
          0,
      );

      farmsToDisplay.map((farm) => {
        if (
          !farm.tokenAmount ||
          !farm.lpTotalInQuoteToken ||
          !farm.lpTotalInQuoteToken
        ) {
          return farm;
        }
        const cakeRewardPerBlock = new BigNumber(rewardPerBlock).times(
          farm.poolWeight,
        );
        const cakeRewardPerYear = cakeRewardPerBlock.times(BLOCKS_PER_YEAR);

        let apy = cakePriceVsBNB
          .times(cakeRewardPerYear)
          .div(farm.lpTotalInQuoteToken);

        if (farm.quoteTokenSymbol === QuoteToken.BUSD) {
          apy = cakePriceVsBNB
            .times(cakeRewardPerYear)
            .div(farm.lpTotalInQuoteToken)
            .times(bnbPrice);
        } else if (farm.quoteTokenSymbol === QuoteToken.CAKE) {
          apy = cakeRewardPerYear.div(farm.lpTokenBalanceMC);
        } else if (farm.quoteTokenSymbol === QuoteToken.X2P) {
          apy = cakeRewardPerYear
            .times(cakePrice)
            .div(farm.lpTokenBalanceMC)
            .div(x2pPrice);
        } else if (farm.dual) {
          const cakeApy =
            farm &&
            cakePriceVsBNB
              .times(cakeRewardPerBlock)
              .times(BLOCKS_PER_YEAR)
              .div(farm.lpTotalInQuoteToken);
          const dualApy =
            farm.tokenPriceVsQuote &&
            new BigNumber(farm.tokenPriceVsQuote)
              .times(farm.dual.rewardPerBlock)
              .times(BLOCKS_PER_YEAR)
              .div(farm.lpTotalInQuoteToken);

          apy = cakeApy && dualApy && cakeApy.plus(dualApy);
        }

        if (maxAPY.current < apy.toNumber()) maxAPY.current = apy.toNumber();

        return apy;
      });
    },
    [bnbPrice, farmsLP, rewardPerBlock, x2pPrice, cakePrice],
  );

  return (
    <StyledFarmStakingCard>
      <CardBody>
        <Heading color="contrast" size="lg">
          Earn up to
        </Heading>
        <CardMidContent color="#7645d9">
          {getHighestAPY() ? (
            `${getHighestAPY()}%`
          ) : (
            <Skeleton animation="pulse" variant="rect" height="44px" />
          )}
        </CardMidContent>
        <Flex justifyContent="space-between">
          <Heading color="contrast" size="lg">
            in Farms
          </Heading>
          <NavLink exact activeClassName="active" to="/farms" id="farm-apy-cta">
            <ArrowForwardIcon mt={30} color="primary" />
          </NavLink>
        </Flex>
      </CardBody>
    </StyledFarmStakingCard>
  );
};

export default EarnAPYCard;
